import { FC } from 'react';
// import Link from 'next/link';
import { LidoLogo } from '@lidofinance/lido-logo';
import logo from "../../scythesymbol.png";
import {
  HeaderStyle,
  HeaderLogoStyle,
  HeaderActionsStyle,
} from './headerStyles';
import {Button,ButtonIcon,Stake} from '@lidofinance/lido-ui';
import HeaderWallet from './headerWallet';
import { Link,useMatch } from "react-router-dom";

const HeaderLink = (props) => {

  return (
    <Link to={props.href}>
      {props.children}
    </Link>
  )
}


const Header = () => {

  const ftmStaking = useMatch('/ftm');
  const usdStaking = useMatch('/usd');

  console.log(ftmStaking,usdStaking);

  return (
    <HeaderStyle size="full" forwardedAs="header" style={{position:'fixed',zIndex:500}}>
      <HeaderLogoStyle>
        <HeaderLink href="/">
          <img src={logo} style={{height:'30px'}} />
          {/* <LidoLogo /> */}
        </HeaderLink>
      </HeaderLogoStyle>
      <HeaderLink href='/ftm'>
        <ButtonIcon
          color="secondary"
          size="sm"
          icon={<Stake />}
          variant="ghost"
          style={{color:ftmStaking?'white':''}}
        >
          FTM Vault
        </ButtonIcon>
      </HeaderLink>
      <HeaderLink href='/usd'>
        <ButtonIcon
          color="secondary"
          size="sm"
          icon={<Stake />}
          variant="ghost"
          style={{color:usdStaking?'white':''}}
        >
          USD Vault
        </ButtonIcon>
      </HeaderLink>
      <HeaderActionsStyle>
        <HeaderWallet />
      </HeaderActionsStyle>
    </HeaderStyle>
  );
}

export default Header;
