
import { createRef, useEffect, useState } from 'react';
import { H2,Text,Block,InputGroup,SelectIcon,Option,Input,Button,DataTable,DataTableRow,Accordion } from '@lidofinance/lido-ui';
import StakeIcon from './stakeIcons';
import { useEthers,useCall, useTokenAllowance } from "@usedapp/core";
import { Contract } from "@ethersproject/contracts";
import { ethers } from "ethers";
import {SliderContainerStyle, SliderHolderStyle, SliderOptionStyle,SliderStyle} from "./stakingStyles.js";
import BalanceViewer from "./balances";
import { formatEther } from '@ethersproject/units'

const unizapabi = [{"inputs":[{"internalType":"address","name":"_router","type":"address"},{"internalType":"address","name":"_WETH","type":"address"}],"stateMutability":"nonpayable","type":"constructor"},{"inputs":[],"name":"WETH","outputs":[{"internalType":"address","name":"","type":"address"}],"stateMutability":"view","type":"function"},{"inputs":[{"internalType":"address","name":"beefyVault","type":"address"},{"internalType":"uint256","name":"tokenAmountOutMin","type":"uint256"},{"internalType":"address","name":"tokenIn","type":"address"},{"internalType":"uint256","name":"tokenInAmount","type":"uint256"}],"name":"beefIn","outputs":[],"stateMutability":"nonpayable","type":"function"},{"inputs":[{"internalType":"address","name":"beefyVault","type":"address"},{"internalType":"uint256","name":"tokenAmountOutMin","type":"uint256"}],"name":"beefInETH","outputs":[],"stateMutability":"payable","type":"function"},{"inputs":[{"internalType":"address","name":"beefyVault","type":"address"},{"internalType":"uint256","name":"withdrawAmount","type":"uint256"}],"name":"beefOut","outputs":[],"stateMutability":"nonpayable","type":"function"},{"inputs":[{"internalType":"address","name":"beefyVault","type":"address"},{"internalType":"uint256","name":"withdrawAmount","type":"uint256"},{"internalType":"address","name":"desiredToken","type":"address"},{"internalType":"uint256","name":"desiredTokenOutMin","type":"uint256"}],"name":"beefOutAndSwap","outputs":[],"stateMutability":"nonpayable","type":"function"},{"inputs":[],"name":"checkWETH","outputs":[{"internalType":"bool","name":"isValid","type":"bool"}],"stateMutability":"view","type":"function"},{"inputs":[{"internalType":"address","name":"beefyVault","type":"address"},{"internalType":"address","name":"tokenIn","type":"address"},{"internalType":"uint256","name":"fullInvestmentIn","type":"uint256"}],"name":"estimateSwap","outputs":[{"internalType":"uint256","name":"swapAmountIn","type":"uint256"},{"internalType":"uint256","name":"swapAmountOut","type":"uint256"},{"internalType":"address","name":"swapTokenOut","type":"address"}],"stateMutability":"view","type":"function"},{"inputs":[],"name":"minimumAmount","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"stateMutability":"view","type":"function"},{"inputs":[],"name":"router","outputs":[{"internalType":"contract IUniswapV2Router02","name":"","type":"address"}],"stateMutability":"view","type":"function"},{"stateMutability":"payable","type":"receive"}]
const ERC20abi = [{"constant":true,"inputs":[],"name":"name","outputs":[{"name":"","type":"string"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"_spender","type":"address"},{"name":"_value","type":"uint256"}],"name":"approve","outputs":[{"name":"","type":"bool"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"totalSupply","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"_from","type":"address"},{"name":"_to","type":"address"},{"name":"_value","type":"uint256"}],"name":"transferFrom","outputs":[{"name":"","type":"bool"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"decimals","outputs":[{"name":"","type":"uint8"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[{"name":"_owner","type":"address"}],"name":"balanceOf","outputs":[{"name":"balance","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"symbol","outputs":[{"name":"","type":"string"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"_to","type":"address"},{"name":"_value","type":"uint256"}],"name":"transfer","outputs":[{"name":"","type":"bool"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[{"name":"_owner","type":"address"},{"name":"_spender","type":"address"}],"name":"allowance","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"payable":true,"stateMutability":"payable","type":"fallback"},{"anonymous":false,"inputs":[{"indexed":true,"name":"owner","type":"address"},{"indexed":true,"name":"spender","type":"address"},{"indexed":false,"name":"value","type":"uint256"}],"name":"Approval","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"name":"from","type":"address"},{"indexed":true,"name":"to","type":"address"},{"indexed":false,"name":"value","type":"uint256"}],"name":"Transfer","type":"event"}]

function feetopercent(fee){
    if(!fee){
        return 'error';
    }
    return (fee.toNumber()/100).toFixed(2)
}
var truncateDecimals = function (number, digits) {
    var multiplier = Math.pow(10, digits),
        adjustedNum = number * multiplier,
        truncatedNum = Math[adjustedNum < 0 ? 'ceil' : 'floor'](adjustedNum);

    return truncatedNum / multiplier;
}

const Staking = (props) => {

    const config = props.settings;

    // console.log(config.enter);

    const { account, activateBrowserWallet,library, deactivate, error } = useEthers();

    const [currency,setCurrency] = useState(props.settings.depositCurrencies[0]);
    const [amountRaw,setAmount] = useState('');
    const [fee,setFee] = useState(0);
    const [lpOut,setLpOut] = useState(0);
    const [isStaking,setStaking] = useState(true);

    

    const iu = !!config.LP;
    const doubleLiq = iu&&!isStaking&&currency.includes('+')
    const liqcoins = doubleLiq?currency.split('+'):currency;
    const isWant = iu&&config.vaultAddress==config.currencies[currency];


    console.log(currency,config);
    //||parseFloat(amountRaw).toString().includes('e')
    const amount = (amountRaw==''||isNaN(parseFloat(amountRaw)))?
        ((isWant&&iu)?'0.000000000000001':'0.0001'):
        (!iu?
            amountRaw.toString():
            amountRaw.toString()
            );
//
    const toosmall = amount=='0.0001'||parseFloat(amount)<=0.000000000000001;
    
    const { error: feeError, value: fees } =
        useCall({
            contract: new Contract(config.contract.address,config.contract.abi),
            method: isStaking?"buyback":"reflection",
        }) ?? {};
    
    //estimateDepositETHScythe
    
    
    console.log(config.estimates.deposit[currency]);
    const { error: outEstError, value: outEst } = useCall({
            contract: new Contract(config.enter.address,config.enter.abi),
            method: config.estimates.deposit[currency].shares,
            args : [iu?(lpOut):(amount?ethers.utils.parseEther(amount):0)]
        }) ?? {};
    

    const { error: outmEstError, value: minoutEst } = useCall({
            contract: new Contract(config.enter.address,config.enter.abi),
            method: config.estimates.deposit[currency].minshares,
            args : [iu?(lpOut):(amount?ethers.utils.parseEther(amount):0)]
        }) ?? {};
    
    var out = outEst?outEst[0]:0;
    

    var useMin = false;
    if(outEst&&minoutEst){
        if(minoutEst[0].gt(outEst[0])){
            out = minoutEst[0];
            useMin = true;
        

        }
    }

    const { error: swapEstError, value: swapEst } =
        useCall({
            contract: iu?new Contract(config.zap,unizapabi):new Contract(config.enter.address,config.enter.abi),
            method: isWant?"halving":"estimateSwap",
            args : (iu)?(isWant?null:[config.vaultAddress,config.currencies[currency],ethers.utils.parseUnits(amount,config.tokenDecimals[currency])]):[amount?ethers.utils.parseEther(amount):0]
        }) ?? {};
    
    
    
    const { error: scytheEstError, value: scytheEst } =
        useCall({
            contract: new Contract(config.enter.address,config.enter.abi),
            method: config.estimates.deposit[currency].scythe,
            args : (iu&&!isWant)?[lpOut?lpOut:0]:[amount?ethers.utils.parseEther(amount):0]
        }) ?? {};
    

    const { error: tokensoutError, value: tokensOut } =
        useCall({
            contract: new Contract(config.contract.address,config.contract.abi),
            method: "estTokensFromShare",
            args : isStaking?[0]:[amount?ethers.utils.parseEther(amount).div(config.sharemult):0]
        }) ?? {};
    const { error: ethoutError, value: ethOut } =
        useCall({
            contract: new Contract(config.enter.address,config.enter.abi),
            method: iu?(doubleLiq?"estTokensFromShareLP":"halving"):"estETHFromShare",
            args : iu?(doubleLiq?[ethers.utils.parseEther(amount).div(config.sharemult),config.currencies[currency.split('+')[0]],config.currencies[currency.split('+')[1]]]:[]):(isStaking?[1]:[amount?ethers.utils.parseEther(amount):0])
        }) ?? {};
    
    const { error:  vaultRateError, value: vaultToPool } =
        useCall({
            contract: new Contract(config.vaultAddress,config.contract.abi),
            method: "getPricePerFullShare",
        }) ?? {};
    const { error:  vaultRate2Error, value: vault2shares } =
        useCall({
            contract: new Contract(config.contract.address,config.contract.abi),
            method: "getPricePerFullShare",
        }) ?? {};
    var currencyFromShare = 0;
    if(currency=='FTM'){
        currencyFromShare = ethOut?ethOut[0]:0;
    }else if(currency=='beFTM'){
        currencyFromShare = (tokensOut&&vaultToPool)?tokensOut[0].mul(vaultToPool[0]).div(ethers.utils.parseEther('1')):0;
    }else if(currency=='moobeFTM'){
        currencyFromShare = (tokensOut)?tokensOut[0]:0;
    }else{
        if(tokensOut){
            console.log('tokensOut',tokensOut[0]);
        }
        currencyFromShare = (tokensOut)?tokensOut[0]:0;;
    }
    console.log(iu?(doubleLiq?"estTokensFromShareLP":"halving"):"estETHFromShare",iu?(doubleLiq?ethers.utils.parseEther(amount).div(config.sharemult):[]):(isStaking?[1]:[amount?ethers.utils.parseEther(amount):0]))
    if(ethOut){
        console.log("ETH OUT",ethOut)
    }
    
    const xDepot = (vault2shares&&amount&&fees)?(amount*(1-fees[0].toNumber()/10000)/formatEther(vault2shares[0])):0;
    useEffect(function(){
        if(iu&&swapEst&&window.signer){
            (async function(){
                console.log('SWAPEST',ethers.utils.formatUnits(swapEst.swapAmountIn,config.tokenDecimals[currency]));
                const tokenIn = new ethers.Contract(config.currencies[currency],ERC20abi,window.signer);
                const pairLP = new ethers.Contract(config.LP,ERC20abi,window.signer);

                const oneether = ethers.utils.parseEther('1');

                const tokenspershare = (await tokenIn.balanceOf(config.LP)).mul(oneether).div(await pairLP.totalSupply()).mul(2);
                const outlp = ethers.utils.parseUnits(amount,config.tokenDecimals[currency]).mul(oneether).div(tokenspershare).mul(10000).div(10015);

                setLpOut(outlp)
                
                console.log('tokens PER SHARE ',tokenspershare.toString(),' LP OUT ',ethers.utils.formatEther(outlp));

                // await 
            })();
            

        }
    },[swapEst]);
    
    useEffect(function(){
        if(account&&window.signer){
            const contract = new ethers.Contract(config.contract.address,config.contract.abi,window.signer);

            
            const enter = new Contract(config.enter.address,config.enter.abi,window.signer);
            // setEnter(enter);

            window.enter = enter;
            window.scytheLock = contract;

            console.log('CONTRACT REE',window.signer,account)
            contract.balanceOf(account).then(function(b){
                console.log('CONTRACT REE',ethers.utils.formatEther(b));
            })
            //console.log('CURRENT SHARES: ',await contract.balanceOf(account));
            

        }
    }, [account]);

    const ref = createRef();

    async function stakeTokens(){
        console.log('staking ',currency,amount)
        if(!account){
            activateBrowserWallet();
            return;
        }
        try{
            
            if(isStaking){
                window.changeAlert(true,{
                    title: 'Depositing '+currency,
                    subtitle: 'You are currently depositing '+amount+' '+currency,
                    body: 'Please approve the transactions in your metamask wallet as they come up',
                    icon:'loader'
                })
                await config.deposit(currency,amount,window.enter,window.scytheLock,account,useMin);
                window.changeAlert(true,{
                    title: 'Deposited '+currency,
                    subtitle: 'You have deposited '+amount+' '+currency,
                    body: '',
                    icon:'success'
                })
            }else{
                window.changeAlert(true,{
                    title: 'Withdrawing to '+currency,
                    subtitle: 'You are currently withdrawing '+amount+' Shares',
                    body: 'Please approve the transactions in your metamask wallet as they come up',
                    icon:'loader'
                })
                await config.withdraw(currency,amount,window.enter,window.scytheLock,account,useMin);
                window.changeAlert(true,{
                    title: 'Withdrew to '+currency,
                    subtitle: 'You have withdrawn '+amount+' Shares',
                    body: '',
                    icon:'success'
                })
            }
        } catch(e){
            if(e.code===4001&&window.changeAlert){
                window.changeAlert(true,{
                    title: 'Error',
                    subtitle: 'User denied transaction',
                    body: 'Please try again',
                    icon:'error'
                  })
            }
            console.log('error',e)
        }
        
        
    }
    async function useMax(){
        console.log('USE MAX')
        if(isStaking){
            if(currency=='FTM'){
                setAmount(formatEther((await window.provider.getBalance(account)).sub(ethers.utils.parseEther('2'))))
            }else{
                const contract = new ethers.Contract(config.currencies[currency],config.contract.abi,window.signer);
                const max = ethers.utils.formatUnits((await contract.balanceOf(account)),config.tokenDecimals[currency]||18);
                console.log(max);

                setAmount(max);
            }
        }else{
            setAmount(formatEther((await window.scytheLock.balanceOf(account)).mul(config.sharemult)));
        }
    }

    function changeStaking(){
        console.log('changeStaking')
        setCurrency(!isStaking?config.depositCurrencies[0]:config.withdrawCurrencies[0]);
        setStaking(!isStaking)
        
        setAmount('')
    }

    var stats
    stats = [
        {
            title: 'Scythe Buyback Fee',
            value: (fees?feetopercent(fees[0]):'err')+'%',
        },
        {
            title: 'Deposit Bonus',
            value: (scytheEst?(ethers.utils.formatEther(scytheEst[0])-0).toFixed(3):'err')+" SCYTHE",
            strong: "success"
        },
        {
            title: 'Vault Shares Recieved',
            value: (config.estimates.deposit[currency].direct?(xDepot*config.sharemult).toFixed(3):(outEst?((ethers.utils.formatEther(out)*config.sharemult)).toFixed(3):'err'))+' Shares',
            strong: "primary"
        }
    ]
    if(config.hasSwap.includes(currency)){
        stats.splice(1,0,{
            title:'Swap Bonus',
            value: ((outEst&&swapEst&&amount)?Math.max(0,(swapEst[0].mul(1000).div(ethers.utils.parseEther(amount)).toNumber()-1000)/10).toFixed(2):'...')+'%',
            help:'By swapping to an asset backed 1:1 with the target you are gaining slightly more tokens! These will all be redeemable for the underlying asset 1:1 when beFTM unlocks',
            strong:'success'
        })
    }
    if(!isStaking){
        stats = [
            {
                title: 'Reflection',
                help: 'Reflections in scythe are dynamic withdrawl fees no greater than 20% which provide bonus yeild to people using the vault',
                value: (fees?feetopercent(fees[0]):'err')+'%',
            },
            {
                title: 'est '+currency+' Recieved',
                value: doubleLiq?((ethOut)?([0,1].map(x=>(ethers.utils.formatUnits(ethOut[x],config.tokenDecimals[liqcoins[x]])*1).toFixed(3)+' '+liqcoins[x]+' ')):'--'):(((currencyFromShare)?(currency=="mooSpiritUSDC-DAI"?parseFloat(ethers.utils.formatEther(currencyFromShare)-0).toString():(ethers.utils.formatEther(currencyFromShare)-0).toFixed(3)):'--')+' '+currency),
                strong: 'primary'
            }
        ]
        if(config.hasSwap.includes(currency)){
            stats.splice(1,0,{
                title:'Swap to FTM fees',
                value: ((tokensOut&&ethOut&&vaultToPool&&(formatEther(tokensOut[0])-0))?(100*(1-(formatEther(ethOut[0])/(formatEther(tokensOut[0])*formatEther(vaultToPool[0]))))).toFixed(2):'...')+'%',
                help:'You are swapping from a 1:1 FTM-backed asset into FTM before it is programatically unlocked. You can eliminate this fee by withdrawing beFTM and redeeming it when unlocked',
                strong:'warning'
            })
        }
    }

    return (
      <>
        <div style={{textAlign:'center'}}>
            <H2 style={{marginBottom:'14px'}} color="text">{isStaking?"Deposit":"Withdraw"} {config.symbol}</H2>
            <h4 style={{marginBottom:'16px',color:"rgba(255, 255, 255, 0.8)",fontWeight:"500"}}>Stake your {config.symbol} to earn {config.symbol} or fully backed assets, no emission based yield</h4>
            <SliderContainerStyle>
                <SliderHolderStyle onClick={changeStaking}>
                    <SliderStyle style={{"left":isStaking?'2px':'calc(100% - 152px)'}}></SliderStyle>
                    <SliderOptionStyle>
                        Deposit
                    </SliderOptionStyle>
                    <SliderOptionStyle>
                        Withdraw
                    </SliderOptionStyle>
                </SliderHolderStyle>
            </SliderContainerStyle>
            <Block  style={{padding:'0px 0px 0px 0px'}}>
                <BalanceViewer settings={config} />
            <Block color="accent" >
            
                <InputGroup fullwidth ref={ref} style={{marginBottom:'16px'}}>
                    <SelectIcon fullwidth
                        anchorRef={ref}
                        icon={<StakeIcon coin={currency} />}
                        onChange={function noRefCheck(event){
                            setCurrency(event);
                            // console.log(event)
                        }}
                        value={currency}
                    >
                        {
                            (isStaking?props.settings.depositCurrencies:props.settings.withdrawCurrencies).map((currency,index) => {
                                return <Option
                                        leftDecorator={<StakeIcon coin={currency} />}
                                        value={currency}
                                        >
                                        {currency}
                                        </Option>
                            })
                        }
                    </SelectIcon>
                    <Input fullwidth
                        placeholder="Amount"
                        label={isStaking?"Amount":"Shares"}
                        rightDecorator={
                            <Button color="primary" onClick={useMax} size="xs" variant="translucent">
                                {(isStaking&&currency=='FTM')?'Max-2':'Max'}
                            </Button>
                        }
                        value={amountRaw}
                        onChange={(event)=>{setAmount(event.target.value)}}
                    />
                </InputGroup>
                
                <Button disabled={(!amount||toosmall)&&account} style={{marginBottom:'16px'}} color="primary" fullwidth size="md" variant="filled" onClick={stakeTokens}>
                    {account?(isStaking?"Deposit":"Withdraw"):'Connect Wallet'}
                </Button>
                <div style={{marginBottom:'16px',textAlign:'left'}}>
                    <DataTable style={{display:''}}>
                        {stats.map(function(stat){
                            return (
                                <DataTableRow loading={!fees||!(outEst&&(swapEst||doubleLiq||isWant)&&amount)} title={stat.title} help={stat.help}>
                                    <Text color={stat.strong} size="xs" strong={!!stat.strong}>{stat.value}</Text>
                                </DataTableRow>
                            )
                        })}
                    </DataTable>
                </div>
                </Block>
            </Block>
            
        </div>
        {
            config.info.map(function(info){
                return (
                    <Accordion
                        style={{marginTop:'16px'}}
                        summary={info.summary}
                    >
                        {info.details}
                    </Accordion> 
                )
            })
        }
        {/* */}
      </>
    );
  }
  
  export default Staking;